import React, { Component } from "react";
import { Button } from "reactstrap";
import {
  CircularProgressbarWithChildren,
  buildStyles
} from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

export default class Result extends Component {
  render() {
    let resultdata = this.props.location.state.result;
    // console.log(resultdata);
    return (
      <>
      {resultdata ? 
        <div
          style={{
            backgroundColor: "#000000",
            minHeight: "100vh"
          }}
        >
          <label className="text-white" style={{ textTransform: "uppercase", fontFamily: "roisbold" }}>
            <b>#5wayz</b>
          </label>
          <h1
            className="text-white"
            style={{ textTransform: "uppercase", alignItems: "flex-start", fontFamily: "roismedium" }}
          >
            y/our
            <br /> result
          </h1>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              fontFamily: "roismedium"
            }}
          >
            <div
              className="d-block d-lg-none"
              style={{ width: "100%", paddingRight: 5, paddingLeft: 5 }}
            >
              <CircularProgressbarWithChildren
                value={resultdata ? resultdata.circlee : null}
                strokeWidth={2}
                styles={buildStyles({
                  pathColor: "#FFF",
                  trailColor: "transparent",
                  strokeLinecap: "butt"
                })}
              >
                <div style={{ width: "100%", padding: 20 }}>
                  <CircularProgressbarWithChildren
                    value={resultdata ? resultdata.circleb : null}
                    strokeWidth={3}
                    styles={buildStyles({
                      pathColor: "#FFF",
                      trailColor: "transparent",
                      strokeLinecap: "butt"
                    })}
                  >
                    <div style={{ width: "100%", padding: 25 }}>
                      <CircularProgressbarWithChildren
                        value={resultdata ? resultdata.circles : null}
                        strokeWidth={3}
                        styles={buildStyles({
                          pathColor: "#FFF",
                          trailColor: "transparent",
                          strokeLinecap: "butt"
                        })}
                      >
                        <div style={{ width: "100%", padding: 25 }}>
                          <CircularProgressbarWithChildren
                            value={resultdata ? resultdata.circlem : null}
                            strokeWidth={4}
                            styles={buildStyles({
                              pathColor: "#FFF",
                              trailColor: "transparent",
                              strokeLinecap: "butt"
                            })}
                          >
                            <div style={{ width: "100%", padding: 30 }}>
                              <CircularProgressbarWithChildren
                                value={resultdata ? resultdata.circlep : null}
                                strokeWidth={4}
                                styles={buildStyles({
                                  pathColor: "#FFF",
                                  trailColor: "transparent",
                                  strokeLinecap: "butt"
                                })}
                              >
                                <div style={{ fontSize: 12, marginTop: -5 }}>
                                </div>
                              </CircularProgressbarWithChildren>
                            </div>
                          </CircularProgressbarWithChildren>
                        </div>
                      </CircularProgressbarWithChildren>
                    </div>
                  </CircularProgressbarWithChildren>
                </div>
              </CircularProgressbarWithChildren>
            </div>

            <div className="d-none d-lg-block" style={{ width: "80%" }}>
              <div style={{ width: "100%" }}>
                <CircularProgressbarWithChildren
                  value={resultdata ? resultdata.circlee : null}
                  strokeWidth={2}
                  styles={buildStyles({
                    pathColor: "#FFF",
                    trailColor: "transparent",
                    strokeLinecap: "butt"
                  })}
                >
                  <div style={{ width: "100%", padding: 40 }}>
                    <CircularProgressbarWithChildren
                      value={resultdata ? resultdata.circleb : null}
                      strokeWidth={2.5}
                      styles={buildStyles({
                        pathColor: "#FFF",
                        trailColor: "transparent",
                        strokeLinecap: "butt"
                      })}
                    >
                      <div style={{ width: "100%", padding: 45 }}>
                        <CircularProgressbarWithChildren
                          value={resultdata ? resultdata.circles : null}
                          strokeWidth={2.5}
                          styles={buildStyles({
                            pathColor: "#FFF",
                            trailColor: "transparent",
                            strokeLinecap: "butt"
                          })}
                        >
                          <div style={{ width: "100%", padding: 50 }}>
                            <CircularProgressbarWithChildren
                              value={resultdata ? resultdata.circlem : null}
                              strokeWidth={3}
                              styles={buildStyles({
                                pathColor: "#FFF",
                                trailColor: "transparent",
                                strokeLinecap: "butt"
                              })}
                            >
                              <div style={{ width: "100%", padding: 50 }}>
                                <CircularProgressbarWithChildren
                                  value={resultdata ? resultdata.circlep : null}
                                  strokeWidth={3}
                                  styles={buildStyles({
                                    pathColor: "#FFF",
                                    trailColor: "transparent",
                                    strokeLinecap: "butt"
                                  })}
                                >
                                  <div style={{ fontSize: 12, marginTop: -5 }}>
                                  </div>
                                </CircularProgressbarWithChildren>
                              </div>
                            </CircularProgressbarWithChildren>
                          </div>
                        </CircularProgressbarWithChildren>
                      </div>
                    </CircularProgressbarWithChildren>
                  </div>
                </CircularProgressbarWithChildren>
              </div>
            </div>
            <br />
            <br />
            <div className="row">
              <div className="col">
                <Button
                  className="btn-lg text-dark"
                  onClick={this.setRedirect}
                  style={{
                    textTransform: "uppercase",
                    backgroundColor: "white",
                    borderRadius: 0
                  }}
                >
                  <b>Others</b>
                </Button>
              </div>
              <div className="col">
                <Button
                  className="btn-lg text-dark"
                  onClick={this.setRedirect}
                  style={{
                    textTransform: "uppercase",
                    backgroundColor: "white",
                    borderRadius: 0
                  }}
                >
                  <b>Share</b>
                </Button>
              </div>
            </div>
            <br />
          </div>
        </div>
         : <div>Not Data Available</div>}
      </>
    );
  }
}
