import React from "react";
import styled from "styled-components";

const sliderThumbStyles = props => `
  width: 25px;
  height: 25px;
  background: #000000;
  cursor: pointer;
  outline: 5px solid #333;
  -webkit-transition: .2s;
  transition: opacity .2s;
`;

const Styles = styled.div`
  display: flex;
  align-items: center;
  color: #888;
  margin-top: 2rem;
  margin-bottom: 2rem;
  .value {
    flex: 1;
    font-size: 2rem;
  }
  .slider {
    flex: 6;
    -webkit-appearance: none;
    width: 100%;
    height: 15px;
    background: #000000;
    outline: none;
    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      appearance: none;
      ${props => sliderThumbStyles(props)}
    }
    &::-moz-range-thumb {
      ${props => sliderThumbStyles(props)}
    }
  }
`;

const InputField = ({
  field, // { name, value, onChange, onBlur }
  form: { touched, errors }, // also values, setXXXX, handleXXXX, dirty, isValid, status, etc.
  ...props
}) => (
  <Styles>
    <div className="value"><b>NO</b></div>
    <input
      {...field}
      {...props}
      type="range"
      min={0}
      max={100}
      className="slider"
    />
    <div className="value"><b>YES</b></div>
  </Styles>
);

export default InputField;
