import React, { Component } from "react";
import { Form1 } from "../components/Form1";
import { Form2 } from "../components/Form2";
import { Form3 } from "../components/Form3";
import { Form4 } from "../components/Form4";
import { Form5 } from "../components/Form5";
import { Form6 } from "../components/Form6";
import { Form7 } from "../components/Form7";
import { Form8 } from "../components/Form8";
import { Form9 } from "../components/Form9";
import { Form10 } from "../components/Form10";
import { Form11 } from "../components/Form11";
import { Form12 } from "../components/Form12";
import { Form13 } from "../components/Form13";
import { Form14 } from "../components/Form14";
import { Form15 } from "../components/Form15";
import { Form16 } from "../components/Form16";
import { Form17 } from "../components/Form17";
import { Form18 } from "../components/Form18";
import { Form19 } from "../components/Form19";
import { Form20 } from "../components/Form20";
import { Form21 } from "../components/Form21";
import { Form22 } from "../components/Form22";
import { Form23 } from "../components/Form23";
import { Form24 } from "../components/Form24";
import { Form25 } from "../components/Form25";
import { Form, Formik } from "formik";
import { Button } from "reactstrap";
import { Redirect } from "react-router-dom";

const pages = [
  <Form1 />,
  <Form2 />,
  <Form3 />,
  <Form4 />,
  <Form5 />,
  <Form6 />,
  <Form7 />,
  <Form8 />,
  <Form9 />,
  <Form10 />,
  <Form11 />,
  <Form12 />,
  <Form13 />,
  <Form14 />,
  <Form15 />,
  <Form16 />,
  <Form17 />,
  <Form18 />,
  <Form19 />,
  <Form20 />,
  <Form21 />,
  <Form22 />,
  <Form23 />,
  <Form24 />,
  <Form25 />
];

let questionpages;

export default class Question extends Component {
  constructor(props) {
    super(props);
    this.state = {
      questions: [],
      redirect: false,
      page: 0,
      circlep: 0,
      circlem: 0,
      circles: 0,
      circleb: 0,
      circlee: 0
    };
  }
  setRedirect = () => {
    this.setState({
      redirect: true
    });
    console.log(this.state);
  };

  renderRedirect = () => {
    if (this.state.redirect) {
      return (
        <Redirect to={{ pathname: "/result", state: { result: this.state } }} />
      );
    }
  };

  componentDidMount() {
    // this.fetchQuestion();
  }

  async fetchQuestion() {
    const response = await fetch(/*add your url here*/);
    const question_response = await response.json();
    this.setState({
      questions: question_response,
      page: question_response.length
    });
  }

  handleSubmit = values => {
    console.log("values: ", values);
    let vcirclep;
    let vcirclem;
    let vcircles;
    let vcircleb;
    let vcirclee;

    vcirclep =
      (values.form1 +
        values.form2 +
        values.form3 +
        values.form4 +
        values.form5) /
      5;
    vcirclem =
      (values.form6 +
        values.form7 +
        values.form8 +
        values.form9 +
        values.form10) /
      5;
    vcircles =
      (values.form11 +
        values.form12 +
        values.form13 +
        values.form14 +
        values.form15) /
      5;
    vcircleb =
      (values.form16 +
        values.form17 +
        values.form18 +
        values.form19 +
        values.form20) /
      5;
    vcirclee =
      (values.form21 +
        values.form22 +
        values.form23 +
        values.form24 +
        values.form25) /
      5;
    console.log(vcirclep, vcirclem, vcircles, vcircleb, vcirclee);
    this.setState({
      circlep: vcirclep,
      circlem: vcirclem,
      circles: vcircles,
      circleb: vcircleb,
      circlee: vcirclee
    });

    this.setRedirect();
  };

  nextPage = () => this.setState(state => ({ page: state.page + 1 }));

  render() {
    let background;
    let button;
    if( this.state.page >= 0 && this.state.page <= 4){
      background = "#FF02FD";
      button = "#FFFFFF";
    } else if( this.state.page >= 5 && this.state.page <= 9){
      background = "#FF4600";
      button = "#FFFF00"
    } else if( this.state.page >= 10 && this.state.page <= 14){
      background = "#0000FF";
      button = "#FF4600";
    } else if( this.state.page >= 15 && this.state.page <= 19){
      background = "#00D228";
      button = "#0000FF"
    } else if( this.state.page >= 20 && this.state.page <= 24){
      background = "#ffff00";
      button = "#00D228"
    } else {
     background = "#5ef0f7"
     button = "#ff2569"
    }
    console.log(button);
    return (
      <>
        {this.renderRedirect()}
        <div style={{ minHeight: "100vh", backgroundColor: background }}>
          <label style={{ textTransform: "uppercase" }}>
            <b style={{ color: button, fontFamily: "roisbold" }}>#5wayz</b>
          </label>
          <Formik
            initialValues={{
              form1: 50,
              form2: 50,
              form3: 50,
              form4: 50,
              form5: 50,
              form6: 50,
              form7: 50,
              form8: 50,
              form9: 50,
              form10: 50,
              form11: 50,
              form12: 50,
              form13: 50,
              form14: 50,
              form15: 50,
              form16: 50,
              form17: 50,
              form18: 50,
              form19: 50,
              form20: 50,
              form21: 50,
              form22: 50,
              form23: 50,
              form24: 50,
              form25: 50
            }}
            onSubmit={this.handleSubmit}
            render={formProps => {
              return (
                <Form>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      alignItems: "center",
                      justifyContent: "center",
                      padding: 10,
                      fontFamily: "roismedium"
                    }}
                  >
                    {pages[this.state.page]}
                    <br />
                    {this.state.page === pages.length - 1 ? (
                      <Button
                        className="btn-lg text-dark"
                        type="submit"
                        color="success"
                        style={{
                          textTransform: "uppercase",
                          borderRadius: 0
                        }}
                      >
                        <b>Submit</b>
                      </Button>
                    ) : (
                      <Button
                        className="btn-lg"
                        onClick={this.nextPage}
                        style={{
                          textTransform: "uppercase",
                          borderRadius: 0,
                          backgroundColor: "#000000"
                        }}
                      >
                        <b style={{ color: button }}>Next</b>
                      </Button>
                    )}
                  </div>
                </Form>
              );
            }}
          />
          <br />
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center"
            }}
          ></div>
        </div>
      </>
    );
  }
}
