import React, { Component } from "react";
import { Button } from "reactstrap";
import { Redirect } from "react-router-dom";

class Start extends Component {
  constructor(props) {
    super(props);
    this.state = {
      redirect: false
    };
  }
  setRedirect = () => {
    this.setState({
      redirect: true
    });
    console.log(this.state);
  };

  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to="/survey" />;
    }
  };
  render() {
    return (
      <>
        {this.renderRedirect()}
        <div
          style={{
            backgroundColor: "#282c34",
            minHeight: "100vh"
          }}
        >
          <label className="text-white" style={{ textTransform: "uppercase", fontFamily: "roisbold" }}>
            <b>#5wayz</b>
          </label>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              fontFamily: "roismedium"
            }}
          >
            <h1
              className="text-white"
              style={{ textTransform: "uppercase", alignItems: "flex-start" }}
            >
              y/our
              <br /> framework
            </h1>
            <Button
              className="btn-lg text-dark"
              onClick={this.setRedirect}
              color="success"
              style={{
                textTransform: "uppercase",
                borderRadius: 0
              }}
            >
              <b>Start</b>
            </Button>
          </div>
        </div>
      </>
    );
  }
}

export default Start;
